.card {
  color: #060b35;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: auto;
  padding-top: 32px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: block;
}

.card__text-container {
  text-align: left;
}

.card__title {
  color: #060b35;
  margin: 0 16px 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 4px 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__price {
  justify-content: space-between;
  width: calc(100% - 32px);
  display: flex;
}

.card__price-label {
  color: #616070;
  text-align: left;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  text-align: right;
  margin-left: 85px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}

.card__buy {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
  border-radius: 5px;
}

.stars {
  justify-content: space-between;
  align-items: center;
  gap: 4px;
  width: 100%;
  padding: 0 16px;
  display: flex;
}

.stars__container {
  margin-bottom: 24px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars .stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  color: #060b35;
  margin-bottom: 24px;
  padding-right: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

html, body, section {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Roboto, sans-serif;
  display: flex;
}
